#map {
    height: 100%;
    width: 100%;
}

.googleMapContainer {
    position: relative;
    width: calc(100% - 50px);
    grid-row-start: 1;
    grid-row-end: 5;
    z-index: 0;
}

.mapMenu {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;
}


